h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.page {
  color: #504f5e;
  scroll-behavior: smooth;
  font-family: Playfair Display, sans-serif;
  font-size: 16px;
  line-height: 24px;
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  background-color: #3f5252;
  transition: all .5s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}

.page__body {
  min-width: 320px;
}

.button_red {
  cursor: pointer;
  color: #f1f5f4;
  background-color: #cd4d31;
  border: none;
  width: 100%;
  padding: 22px;
  font-family: Raleway, sans-serif;
  font-weight: 600;
  line-height: 16px;
  transition: all .5s;
}

.button_red:hover {
  background-color: #af3419;
  transition: transform .5s;
}

.button_red:hover:hover {
  transform: scale(1.1);
}

@media (width >= 640px) {
  .button_red {
    width: 280px;
  }
}

.button_transparent {
  cursor: pointer;
  color: #cd4d31;
  background-color: #0000;
  border: 1px solid #cd4d31;
  justify-content: center;
  align-items: center;
  gap: 8px;
  width: 100%;
  min-width: 280px;
  padding: 22px;
  font-family: Raleway, sans-serif;
  font-weight: 600;
  line-height: 16px;
  transition: all .5s;
  display: flex;
}

.button_transparent:hover {
  color: #af3419;
  border: 1px solid #af3419;
  transition: transform .5s;
}

.button_transparent:hover:hover {
  transform: scale(1.1);
}

.button_transparent:after {
  content: "";
  background-image: url("arrow.8951bb24.svg");
  background-repeat: no-repeat;
  background-size: contain;
  width: 20px;
  height: 20px;
  display: block;
}

@media (width >= 640px) {
  .button_transparent {
    width: 210px;
    position: absolute;
    top: 0;
    right: 40px;
  }
}

@media (width >= 1280px) {
  .button_transparent {
    width: 270px;
    right: 95px;
  }
}

